@import '~bootstrap/scss/_functions';
@import '~bootstrap/scss/_variables';
@import '~bootstrap/scss/mixins/_breakpoints';

@font-face {
  font-family: 'Press Start 2P';
  font-style: normal;
  font-weight: 400;
  src: url(http://fonts.gstatic.com/s/pressstart2p/v12/e3t4euO8T-267oIAQAu6jDQyK3nVivNm4I81.woff2)
    format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215,
    U+FEFF, U+FFFD;
  font-display: swap;
}

.intro {
  background-image: url('../../img/banner-sm.jpg');
  background-repeat: no-repeat;
  background-position: 75% 100%;
  background-size: cover;
  color: whitesmoke;
  text-shadow: 3px 3px 3px $dark, 0 0 1px black;
  height: 100%;

  .container {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%;

    h1 {
      font-family: 'Press Start 2P';
    }
    p {
      font-size: 1rem;
      font-weight: bold;
    }
  }
  @include media-breakpoint-down(sm) {
    .container {
      width: 100%;
    }
  }
  @include media-breakpoint-up(md) {
    background-image: url('../../img/banner-md.jpg');
  }
  @include media-breakpoint-up(xl) {
    background-image: url('../../img/banner.jpg');
    background-attachment: fixed;
    background-position-x: 0;
  }
}
